import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';

configure({ enforceActions: 'always' });

class WorkoutStore extends StoreModel {
    constructor() {
        super('workout', {
            namePlural: 'workouts',
            sort: 'title',
            limit: 100,
            api: {
                search: {
                    url: '/api/workouts/',
                    params: {
                        extendedView: 1,
                        limit: 15,
                        sort: 'id',
                    },
                },
                load: {
                    url: '/api/workouts/',
                    params: {},
                },
                save: {
                    url: '/api/workouts/',
                    params: {},
                },
            },
        });
    }

    @observable workout = {};

    @observable newWorkout = {};

    @observable workouts = [];

    @observable workoutLocations = [];

    // @action
    // afterLoad(response) {
    //     console.log(response);
    // }

    @action
    appendLocation(location, verb) {
        verb = verb || 'updated';
        this.workoutLocations.push({ verb, location })

        this.saveField(this.workout.id, 'tracks', this.workoutLocations);
    }

    startTracking = () => {
        const options = {
            enableHighAccuracy: false,
            timeout: 5000,
            maximumAge: 0
        };
        if ('geolocation' in navigator) {
            navigator.geolocation.getCurrentPosition(location => this.appendLocation(location));
            this.watchLocationId = navigator.geolocation.watchPosition(
                location => this.appendLocation(location),
                err => console.error(err),
                options,
            );
        } else {
            console.error('Geolocation API not supported.');
        }
    }

    stopTracking() {
        navigator.geolocation.clearWatch(this.watchLocationId);
    }
}

const store = new WorkoutStore();
export default store;
